.backdrop-feedback {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 99;
    overflow: auto;
}

.reviews-thanks {
    position: relative;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 311px;
    overflow: auto;
    padding: 84px 24px 178px 58px;
    border-radius: 15px;
    background-color: getColor('text-third');
    @media screen and(min-width: 768px) {
            width: 516px;
            height: 414px;
            padding: 163px 49px 163px 170px;
        }

    &__btn {
        @include flex;
        position: absolute;
        top: 14px;
        right: 14px;
        background-color: #fcf1e0;
        width: 28px;
        height: 28px;
        border-radius: 50%;
        @media screen and(min-width: 768px){
            top: 24px;
            right: 24px;
            width: 38px;
            height: 38px;
        }
    }

    &__svg {
        width: 8px;
        height: 8px;
        fill: getColor('text-third');
        @media screen and(min-width: 768px){
            width: 11px;
            height: 11px;
        }
    }

    &__title {
        @include text(20px, normal, -0.4px, 600);
        margin-bottom: 8px;
        color: #fcf1e0;
        text-transform: uppercase;
        @media screen and(min-width: 768px){
            @include text(24px, 1.16, -0.48px, 600);

        }
    }

    &__text {
        @include text($lheight: 1.14);
        color: getColor('background-secondary');
        max-width: 165px;
        @media screen and(min-width: 768px) {
            @include text(18px, 1.33, -0.38px, 400);
            max-width: 100%;
        }
    }

    &__img {
        position: absolute;
        bottom: 0;
        left: 0;
        width: 85%;
        height: 60%;
        @media screen and(min-width: 768px){
            width: 55%;
            height: 75%;
        }
    }
}